<template>
    <van-overlay :show="loading" class-name="loading-panel" >
        <div>
            <van-loading type="spinner" color="#1989fa" />
        </div>
    </van-overlay>
    <van-notice-bar
        wrapable
        :scrollable="false"
        text="仅用来测试，请谨慎使用"
    />
    <section class="section-input">
        <van-form @submit="onSubmit">
            <div class="type-switch">
                <van-switch v-model="isText" inactive-color="#ff976a" />
            </div>
            <van-cell-group>
                <van-field
                    v-model="key"
                    name="Key"
                    label="密钥"
                    placeholder="Key"
                    maxlength="16"
                />
                <van-field
                    v-model="iv"
                    name="Iv"
                    label="Iv"
                    placeholder="Iv"
                    maxlength="16"
                />
            </van-cell-group>
            <div v-if="isText" class="type-text">
                <van-field
                    v-model="text"
                    rows="3"
                    autosize
                    label="文本"
                    type="textarea"
                    placeholder="Text"
                />
            </div>
            <div v-else class="type-file">
                <div class="file-wrapper">
                    <van-uploader :after-read="afterRead" :before-delete="beforeDelete" v-model="fileList" :max-count="1" accept="image/*,audio/*,video/*,text/*,application/*,.zip,.rar,.ppt,.pptx" />
                    <p v-if="fileList[0]">
                        {{formatSize(fileList[0].file.size)}}
                    </p>
                </div>
            </div>
        </van-form>
    </section>
    <section class="section-result">
        <div v-if="isText" >
            <van-cell>
                <div class="text-result" v-if="textResult">
                    <p>{{ textResult }}</p>
                </div>
            </van-cell>
        </div>
        <div v-else>
            <div class="file-result" v-if="fileListResult[0] && fileList.length">
                <van-cell-group>
                    <van-cell>
                        <van-field
                            v-model="downloadName"
                            placeholder="请输入文件名"
                            rows="3"
                            type="textarea"    
                        />
                    </van-cell>
                    <van-cell v-if="isDecode" class="media-wrapper">
                        <div v-if="filename.match(/\.(jpg|png|jpeg|gif)$/i)" style="text-align:center;">
                            <img :src="fileListResult[0].src" @click="previewResult" />
                        </div>
                        <div v-if="filename.match(/\.(mov|mp4|mpeg|avi|wmv|flv|rmvb|mp3)$/i)">
                            <video :src="fileListResult[0].src" controls="controls" preload poster="../assets/logo.png" width="480" height="320" webkit-playsinline="" playsinline="" x-webkit-airplay="allow" x5-playsinline="" style="width: 100%; height: 100%; display: block; position: relative;"/>
                        </div>
                    </van-cell>
                    <van-cell>
                        <a :href="fileListResult[0].src" :download="fileResultName" target="_blank" class="download">下载<span>({{formatSize(fileListResult[0].size)}})</span></a>
                    </van-cell>
                </van-cell-group>
            </div>
        </div>
    </section>
    <section class="section-btn-group">
        <van-row>
            <van-col span="12">
                <van-button type="primary" class="btn" @click="checkRules('encode')">加密</van-button>
            </van-col>
            <van-col span="12">
                <van-button type="default" class="btn" @click="checkRules('decode')">解密</van-button>
            </van-col>
        </van-row>
    </section>
    <section class="section-rest-panel">
        <a href="javascript:;" @click="resetData">重置</a>
    </section>
</template>

<script>
import { defineComponent } from 'vue';
import { Notify, ImagePreview } from 'vant';
import { text2Uint8Array, file2ArrayBuffer } from '/@/lib';
import AesWorker from 'worker-loader!/@/lib/aes.worker.js';

function callAesWorker(method,key, iv, payload, callback) {
    const aesWorker = AesWorker();
    aesWorker.postMessage({
        type: 'aes',
        method,
        msg: {
            key,
            iv,
            payload
        }
    });
    aesWorker.onmessage = (result) => {
        callback(result);
    };
}

const InitData = {
    isText: false,
    key: '1234567812345678',
    iv: '1234567812345678',
    //
    text: '',
    textResult: '',
    filename: '',
    fileResultName: '',
    isDecode: false,
    fileList: [

    ],
    fileListResult: []
};

export default defineComponent({
    name: 'Home',
    data(){
        return {
            loading: false,
            ...InitData
        };
    },
    computed: {
        downloadName: {
            get(){
                return this.fileResultName.split(/\.\w+$/)[0];
            },
            set(v){
                const type = this.filename.match(/\.\w+$/)|| [''];
                this.fileResultName = v.replace(/(\t|\n)/g, '') + type[0];
            }
        }
    },
    methods: {
        resetData(){
            location.reload();
        },
        beforeDelete(){
            this.fileListResult = [];
            return true;
        },
        afterRead({ file }) {
            this.fileList = [
                {url: window.URL.createObjectURL(file),  file, isImage: true}
            ];
            this.filename = this.fileResultName = file.name;
        },
        onSubmit() {
            void(0);
        },
        formatSize(size) {
            return size>(1024*1024)? (size/1024/1024).toFixed(2) + 'Mb': (size/1024).toFixed(2) + 'Kb';
        },
        checkRules(method){
            const isText = this.isText;
            if (this.key.length !== 16 || this.iv.length !== 16){
                return Notify({ type: 'warning', message: '请输入16位字符的密钥和Iv' });
            }
            //
            if(isText && !this.text){
                return Notify({ type: 'warning', message: '请输入内容' });
            }
            if (!isText && !this.fileList.length) {
                return Notify({ type: 'warning', message: '请上传文件' });
            }
            //
            this.loading= true;
            if ('encode' === method) {
                return this.encode();
            } else {
                return this.decode();
            }
        },
        setFileName(type){
            if(this.fileList.length){
                const file = this.fileList[0].file;
                const name = file.name.replace(/.\w*$/, ($1) => {
                    return `-${type==='en'? 'en': 'de'}${$1}`;
                });
                this.fileResultName = name;
            }
            return 0;
        },
        async encode(){
            this.isDecode = false;
            if(this.isText){
                const textU8Arr = await text2Uint8Array(this.text);
                callAesWorker('base64encode', this.key, this.iv, textU8Arr, (event) => {
                    this.textResult = event.data;
                    this.loading = false;
                });
            }else{
                const fileArray = await file2ArrayBuffer(this.fileList[0].file);
                callAesWorker('fileencode', this.key, this.iv, new Uint8Array(fileArray), (event) => {
                    const blob = new Blob([event.data.buffer]);
                    const src = window.URL.createObjectURL(blob);
                    this.fileListResult = [
                        {src, isImage: true, file: null, size: blob.size}
                    ];
                    this.setFileName('en');
                    this.loading = false;
                });
            }
        },
        async decode(){
            this.isDecode = true;
            if(this.isText){
                callAesWorker('base64decode', this.key, this.iv, this.text, (event) => {
                    this.textResult = event.data;
                    this.loading = false;
                    if(!this.textResult){
                        Notify('解密失败，请检查参数');
                    }
                });
            }else{
                const fileArray = await file2ArrayBuffer(this.fileList[0].file);
                callAesWorker('filedecode', this.key, this.iv, new Uint8Array(fileArray), (event) => {
                    const result = event.data;
                    if(result.length === 0){
                        this.loading = false;
                        return Notify('解密失败，请检查文件或者参数');
                    }
                    const blob = new Blob([result.buffer]);
                    const mediatype = this.filename.match(/\.(mov|mp4|mpeg|avi|wmv|flv|rmvb|mp3)$/i);
                    let file = blob;
                    if(mediatype){
                        file = new File([blob], `1.${mediatype[1]}`, {type:`audio/${mediatype[1]}`});
                    }
                    const src = window.URL.createObjectURL(file);
               
                    this.fileListResult = [
                        {src: src, isImage: true, size: blob.size}
                    ];
                    this.setFileName('de');
                    this.loading = false;
                });
            }
        },
        previewResult(){
            ImagePreview({
                images: [this.fileListResult[0].src],
                closeable: true
            });
        }
    },
    components: {
       
    },
    mounted(){
        //
    }
});
</script>

