//
function text2Uint8Array (text: string) {
    const blob = new Blob([text], {type: 'text/plain'});
    return new Promise((resolve) => {
        const reader = new FileReader();
        reader.onload = () => {
            const u8Array = new Uint8Array(reader.result as any);
            resolve(u8Array);
        };
        reader.readAsArrayBuffer(blob);
    });
}

function file2ArrayBuffer (file: any) {
    return new Promise((resolve) => {
        const reader = new FileReader();
        reader.onload = function(){
            resolve(this.result);
        };
        reader.readAsArrayBuffer(file);
    });
}

export {
    text2Uint8Array,
    file2ArrayBuffer
};